<template>
  <div id="departamento-auth">
    <transition :name="slide" mode="out-in">
      <router-view></router-view>
    </transition>
  </div>
</template>
<script>
export default {
  name: 'Auth',
  data() {
    return {
      slideLeft: 'cards-slide-left',
      slideRight: 'cards-slide-right',
      slide: ''
    }
  },
  watch: {
    $route(to, from){
      switch(to.name){
        case 'login': {
          if(from.name === 'role'){
            this.slide = this.slideRight
          }
          break;
        }
        case 'role': {
          if(from.name === 'login'){
            this.slide = this.slideLeft
          }
          break;
        }
      }
    }
  }
}
</script>
<style lang="scss">
// todo: refactor in separate scss file
@import '@/assets/scss/_animations.scss';

// CARD SLIDING LEFT
  .cards-slide-left-enter-to{
    opacity: 0;
    animation-duration: .7s;
    animation-fill-mode: both;
    animation-name: fadeInRight;
  }
  .cards-slide-left-enter{
    opacity: 0;
  }
  .cards-slide-left-leave-to{
    opacity: 0;
    animation: fadeInLeft .7s reverse both;
  }

// CARD SLIDING RIGHT
  .cards-slide-right-enter-to{
    opacity: 0;
    animation-duration: .7s;
    animation-fill-mode: both;
    animation-name: fadeInLeft;
  }
  .cards-slide-right-enter{
    opacity: 0;
  }
  .cards-slide-right-leave-to{
    opacity: 0;
    animation: fadeInRight .7s reverse both;
  }
</style>